import { createWebHistory, createRouter } from "vue-router";
import Main from "@/components/main/Main.vue";
import BoardList from "@/components/board/BoardList.vue";
import BoardDetail from "@/components/board/BoardDetail.vue";
import BoardInsert from "@/components/board/BoardInsert.vue";
import Login from "@/components/member/login.vue";
const routes = [

    {
        path: "/login",
        name: "login",
        component: Login
    },
    {
        path: "/",
        name: "Main",
        component: Main,
        meta:{
            requireAuth:true
        }
    },
    {
        path: "/BoardList",  
        name: "BoardList",  // router name
        component: BoardList, 
        meta:{
            requireAuth:true
        } // Path로 이동될 Component
    },
    {
        path: "/BoarDetail",  
        name: "BoarDetail",  // router name
        component: BoardDetail, 
        meta:{
            requireAuth:true
        } // Path로 이동될 Component
    },
    {
        path: "/BoardInsert",  
        name: "BoardInsert",  // router name
        component: BoardInsert,  
        meta:{
            requireAuth:true
        }// Path로 이동될 Component
    }

    
];

const router = createRouter({
    history: createWebHistory(),
    routes,
  });
  
  router.beforeEach((to, from, next) => {
    const isAuthenticated = !!localStorage.getItem('token');
  
    if (to.meta.requireAuth && !isAuthenticated) {
      // 인증이 필요한 페이지이지만 인증되지 않은 경우 로그인 페이지로 리디렉션
      localStorage.setItem('redirectUrl', to.fullPath);
      next({ path: '/login' });
    } else {
      next(); // 인증이 필요 없거나 인증된 경우 페이지를 계속 진행
    }
  });
  
  export default router;