<template>

<div class="container">
  <!-- Heading -->
  <h1>SIGN IN</h1>
  
  <!-- Links -->
  <ul class="links">
    <li>
      <a href="#" id="signin">SIGN IN</a>
    </li>
    <li>
      <a href="#" id="signup">SIGN UP</a>
    </li>
    <li>
      <a href="#" id="reset">RESET</a>
    </li>
  </ul>
  
  <!-- Form -->
  
    <!-- email input -->
    <div class="first-input input__block first-input__block">
       <input type="text" placeholder="ID" class="input" id="email"   v-model="username"/>
    </div>
    <!-- password input -->
    <div class="input__block">
       <input type="password" placeholder="Password" class="input" id="password"  v-model="password" />
    </div>
    <!-- repeat password input -->
   
    <!-- sign in button -->
    <button class="signin__btn" @click="login()">
      Sign in
    </button>
 
  <!-- separator -->
  
</div>



</template>
<script>
 import axios from "axios"
 
export default{
   
    data() {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
test(){
  axios.get("http://localhost:8080/").then(()=>{
    
  })
},
async login() {
  // Check if username and password are provided
  if (!this.username || !this.password) {
    console.error('Username or password is null');
    return;
  }
  
  // URLSearchParams 객체 생성
  const params = new URLSearchParams();
  params.append('username', this.username);
  params.append('password', this.password);

  try {
    // Axios POST 요청
    const response = await axios.post('/api/login', params, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    });
    
    
    
    const token = response.headers['authorization'];

    if (token) {
      localStorage.setItem('token', token); // 로컬 스토리지에 토큰 저장
    } else {
      console.error('Token is missing in response');
    }

    alert('Login successful!');
    // 로그인 성공 후 원하는 페이지로 이동
    this.$router.push(localStorage.getItem('redirectUrl') || '/');
  } catch (error) {
    alert('Login failed. Please check your credentials.');
    console.error('Error:', error);
  }
}
  
}
}
</script>
